// src/pages/hr/jobs.js
import { useState, useEffect } from "react";
import { supabase } from "lib/supabase";
import { useRouter } from "next/router";
import toast, { Toaster } from "react-hot-toast";
import HRSidebar from "@/layouts/hrSidebar";
import HRHeader from "@/layouts/hrHeader";
import { useEditor, EditorContent } from "@tiptap/react";
import StarterKit from "@tiptap/starter-kit";
import { Icon } from "@iconify/react";

export default function HRJobBoard({ mode = "light", toggleMode }) {
    const [jobs, setJobs] = useState([]);
    const [viewMode, setViewMode] = useState("cards");
    const [isSidebarOpen, setIsSidebarOpen] = useState(false);
    const [title, setTitle] = useState("");
    const [description, setDescription] = useState("");
    const [file, setFile] = useState(null);
    const [expiresOn, setExpiresOn] = useState("");
    const router = useRouter();

    // TipTap Editor Setup with Toolbar
    const editor = useEditor({
        extensions: [StarterKit],
        content: description,
        onUpdate: ({ editor }) => setDescription(editor.getHTML()),
        editorProps: {
            attributes: {
                class: `prose max-w-none p-4 border rounded-b-lg focus:outline-none focus:ring-2 focus:ring-[#f05d23] min-h-[200px] ${
                    mode === "dark" ? "bg-gray-700 border-gray-600 text-white" : "bg-white border-gray-300 text-[#231812]"
                }`,
            },
        },
    });

    useEffect(() => {
        if (!localStorage.getItem("hr_session")) {
            router.push("/hr/login");
        } else {
            fetchJobs();
        }
    }, [router]);

    const fetchJobs = async () => {
        const { data, error } = await supabase
            .from("job_openings")
            .select("*")
            .order("created_at", { ascending: false });
        if (error) {
            console.error("Error fetching jobs:", error);
            toast.error("Failed to load job openings.");
        } else {
            const updatedJobs = data.map((job) => ({
                ...job,
                is_expired: new Date(job.expires_on) < new Date(),
            }));
            setJobs(updatedJobs);
        }
    };

    const handleSubmitJob = async (e) => {
        e.preventDefault();
        let fileUrl = null;

        if (file) {
            fileUrl = "https://drive.google.com/example"; // Placeholder
        }

        const jobData = {
            title,
            description: description || null,
            file_url: fileUrl || null,
            expires_on: expiresOn,
            is_expired: false,
        };

        const { error } = await supabase.from("job_openings").insert([jobData]);
        if (error) {
            console.error("Error submitting job:", error);
            toast.error("Failed to submit job opening.");
        } else {
            toast.success("Job opening added successfully!", { icon: "✅" });
            setTitle("");
            setDescription("");
            setFile(null);
            setExpiresOn("");
            editor?.commands.setContent("");
            fetchJobs();
        }
    };

    const handleDeleteJob = async (jobId) => {
        const { error } = await supabase.from("job_openings").delete().eq("id", jobId);
        if (error) {
            console.error("Error deleting job:", error);
            toast.error("Failed to delete job opening.");
        } else {
            toast.success("Job opening deleted successfully!", { icon: "✅" });
            fetchJobs();
        }
    };

    const handleLogout = () => {
        localStorage.removeItem("hr_session");
        toast.success("Logged out successfully!");
        setTimeout(() => router.push("/hr/login"), 1000);
    };

    const handleFileChange = (e) => {
        setFile(e.target.files[0]);
    };

    return (
        <div
            className={`min-h-screen flex flex-col ${
                mode === "dark" ? "bg-gradient-to-b from-gray-900 to-gray-800" : "bg-gradient-to-b from-gray-50 to-gray-100"
            }`}
        >
            <Toaster position="top-right" reverseOrder={false} />
            <HRHeader
                toggleSidebar={toggleSidebar}
                isSidebarOpen={isSidebarOpen}
                mode={mode}
                toggleMode={toggleMode}
                onLogout={handleLogout}
            />
            <div className="flex flex-1">
                <HRSidebar
                    isOpen={isSidebarOpen}
                    mode={mode}
                    onLogout={handleLogout}
                    toggleSidebar={() => setIsSidebarOpen(!isSidebarOpen)}
                />
                <div
                    className={`flex-1 p-6 transition-all duration-300 ${
                        isSidebarOpen ? "md:ml-[300px]" : "md:ml-[80px]"
                    }`}
                >
                    <div className="max-w-4xl mx-auto">
                        {/* Job Posting Form */}
                        <div
                            className={`p-6 rounded-lg shadow-lg mb-8 border-t-4 border-[#f05d23] ${
                                mode === "dark" ? "bg-gray-800" : "bg-white"
                            }`}
                        >
                            <div className="flex items-center mb-4">
                                <Icon icon="mdi:briefcase-plus" className="w-6 h-6 text-[#f05d23] mr-2" />
                                <h3 className={`text-xl font-bold ${mode === "dark" ? "text-white" : "text-[#231812]"}`}>
                                    Add New Job Opening
                                </h3>
                            </div>
                            <form onSubmit={handleSubmitJob} className="space-y-6">
                                <div>
                                    <label
                                        className={`block text-sm font-medium mb-2 ${
                                            mode === "dark" ? "text-gray-300" : "text-[#231812]"
                                        }`}
                                    >
                                        Job Title <span className="text-red-500">*</span>
                                    </label>
                                    <div className="relative">
                                        <Icon
                                            icon="mdi:format-title"
                                            className="absolute left-3 top-1/2 transform -translate-y-1/2 w-5 h-5 text-[#f05d23]"
                                        />
                                        <input
                                            type="text"
                                            value={title}
                                            onChange={(e) => setTitle(e.target.value)}
                                            className={`w-full pl-10 pr-4 py-3 border rounded-lg focus:outline-none focus:ring-2 focus:ring-[#f05d23] transition duration-200 ${
                                                mode === "dark"
                                                    ? "bg-gray-700 border-gray-600 text-white"
                                                    : "bg-gray-50 border-gray-300 text-[#231812]"
                                            }`}
                                            placeholder="e.g., Software Engineer"
                                            required
                                        />
                                    </div>
                                </div>
                                <div>
                                    <label
                                        className={`block text-sm font-medium mb-2 ${
                                            mode === "dark" ? "text-gray-300" : "text-[#231812]"
                                        }`}
                                    >
                                        Description (Optional)
                                    </label>
                                    <div
                                        className={`border rounded-lg overflow-hidden ${
                                            mode === "dark" ? "border-gray-600" : "border-gray-300"
                                        }`}
                                    >
                                        <EditorContent editor={editor} className="min-h-[200px]" />
                                    </div>
                                </div>
                                <div>
                                    <label
                                        className={`block text-sm font-medium mb-2 ${
                                            mode === "dark" ? "text-gray-300" : "text-[#231812]"
                                        }`}
                                    >
                                        Upload File (Optional)
                                    </label>
                                    <div className="relative">
                                        <Icon
                                            icon="mdi:file-upload"
                                            className="absolute left-3 top-1/2 transform -translate-y-1/2 w-5 h-5 text-[#f05d23]"
                                        />
                                        <input
                                            type="file"
                                            accept=".pdf,.docx"
                                            onChange={handleFileChange}
                                            className={`w-full pl-10 pr-4 py-3 border rounded-lg ${
                                                mode === "dark"
                                                    ? "bg-gray-700 border-gray-600 text-gray-300"
                                                    : "bg-gray-50 border-gray-300 text-[#231812]"
                                            }`}
                                        />
                                    </div>
                                </div>
                                <div>
                                    <label
                                        className={`block text-sm font-medium mb-2 ${
                                            mode === "dark" ? "text-gray-300" : "text-[#231812]"
                                        }`}
                                    >
                                        Expires On <span className="text-red-500">*</span>
                                    </label>
                                    <div className="relative">
                                        <Icon
                                            icon="mdi:calendar"
                                            className="absolute left-3 top-1/2 transform -translate-y-1/2 w-5 h-5 text-[#f05d23]"
                                        />
                                        <input
                                            type="date"
                                            value={expiresOn}
                                            onChange={(e) => setExpiresOn(e.target.value)}
                                            className={`w-full pl-10 pr-4 py-3 border rounded-lg focus:outline-none focus:ring-2 focus:ring-[#f05d23] transition duration-200 ${
                                                mode === "dark"
                                                    ? "bg-gray-700 border-gray-600 text-white"
                                                    : "bg-gray-50 border-gray-300 text-[#231812]"
                                            }`}
                                            required
                                        />
                                    </div>
                                </div>
                                <button
                                    type="submit"
                                    className="w-full py-3 bg-[#f05d23] text-white rounded-lg hover:bg-[#d94f1e] transition duration-200 shadow-md flex items-center justify-center gap-2"
                                >
                                    <Icon icon="mdi:plus" width={20} height={20} />
                                    Add Job Opening
                                </button>
                            </form>
                        </div>

                        {/* Job Listings */}
                        <div
                            className={`p-6 rounded-lg shadow-lg ${
                                mode === "dark" ? "bg-gray-800" : "bg-white"
                            }`}
                        >
                            <div className="flex justify-between items-center mb-4">
                                <div className="flex items-center gap-2">
                                    <Icon icon="mdi:briefcase" className="w-6 h-6 text-[#f05d23]" />
                                    <h3 className={`text-xl font-bold ${mode === "dark" ? "text-white" : "text-[#231812]"}`}>
                                        Current Job Openings
                                    </h3>
                                </div>
                                <button
                                    onClick={() => setViewMode(viewMode === "cards" ? "table" : "cards")}
                                    className={`px-3 py-1 rounded-lg flex items-center gap-2 ${
                                        mode === "dark"
                                            ? "bg-gray-700 text-white hover:bg-gray-600"
                                            : "bg-gray-200 text-[#231812] hover:bg-gray-300"
                                    }`}
                                >
                                    <Icon
                                        icon={viewMode === "cards" ? "mdi:table" : "mdi:view-dashboard"}
                                        width={20}
                                        height={20}
                                    />
                                    {viewMode === "cards" ? "Table View" : "Card View"}
                                </button>
                            </div>
                            {jobs.length === 0 ? (
                                <p className={`italic text-center ${mode === "dark" ? "text-gray-400" : "text-gray-500"}`}>
                                    No job openings yet.
                                </p>
                            ) : viewMode === "cards" ? (
                                <div className="grid gap-6 md:grid-cols-2">
                                    {jobs.map((job) => (
                                        <div
                                            key={job.id}
                                            className={`p-4 rounded-lg shadow-md hover:shadow-lg transition duration-200 border-t-2 border-[#f05d23] ${
                                                mode === "dark" ? "bg-gray-700 text-white" : "bg-white text-[#231812]"
                                            } ${job.is_expired ? "opacity-50" : ""}`}
                                        >
                                            <h4 className="text-lg font-semibold mb-2">{job.title}</h4>
                                            {job.description && (
                                                <div
                                                    className="mt-1 prose max-w-none mb-2 text-sm"
                                                    dangerouslySetInnerHTML={{ __html: job.description }}
                                                />
                                            )}
                                            {job.file_url && (
                                                <a
                                                    href={job.file_url}
                                                    className="text-[#f05d23] underline hover:text-[#d94f1e] mt-1 inline-block text-sm"
                                                    target="_blank"
                                                    rel="noopener noreferrer"
                                                >
                                                    View File
                                                </a>
                                            )}
                                            <p className={`text-sm mt-2 ${mode === "dark" ? "text-gray-400" : "text-gray-500"}`}>
                                                Expires: {new Date(job.expires_on).toLocaleDateString()} {job.is_expired && "(Expired)"}
                                            </p>
                                            <button
                                                onClick={() => handleDeleteJob(job.id)}
                                                className="mt-3 px-3 py-1 bg-red-500 text-white rounded-lg hover:bg-red-600 transition duration-200 flex items-center gap-2"
                                            >
                                                <Icon icon="mdi:trash-can" width={20} height={20} />
                                                Delete
                                            </button>
                                        </div>
                                    ))}
                                </div>
                            ) : (
                                <div className="overflow-x-auto">
                                    <table
                                        className={`w-full rounded-lg shadow-md ${
                                            mode === "dark" ? "bg-gray-700 text-white" : "bg-white text-[#231812]"
                                        }`}
                                    >
                                        <thead>
                                        <tr className={`${mode === "dark" ? "bg-gray-600" : "bg-gray-100"}`}>
                                            <th className="p-3 text-left">Title</th>
                                            <th className="p-3 text-left">Description</th>
                                            <th className="p-3 text-left">File</th>
                                            <th className="p-3 text-left">Expires</th>
                                            <th className="p-3 text-left">Actions</th>
                                        </tr>
                                        </thead>
                                        <tbody>
                                        {jobs.map((job) => (
                                            <tr
                                                key={job.id}
                                                className={`border-b ${job.is_expired ? "opacity-50" : ""} ${
                                                    mode === "dark" ? "border-gray-600" : "border-gray-200"
                                                }`}
                                            >
                                                <td className="p-3">{job.title}</td>
                                                <td className="p-3">
                                                    {job.description ? (
                                                        <div
                                                            className="prose max-w-none text-sm"
                                                            dangerouslySetInnerHTML={{ __html: job.description }}
                                                        />
                                                    ) : (
                                                        "N/A"
                                                    )}
                                                </td>
                                                <td className="p-3">
                                                    {job.file_url ? (
                                                        <a
                                                            href={job.file_url}
                                                            className="text-[#f05d23] underline hover:text-[#d94f1e]"
                                                            target="_blank"
                                                            rel="noopener noreferrer"
                                                        >
                                                            View
                                                        </a>
                                                    ) : (
                                                        "N/A"
                                                    )}
                                                </td>
                                                <td className="p-3">
                                                    {new Date(job.expires_on).toLocaleDateString()} {job.is_expired && "(Expired)"}
                                                </td>
                                                <td className="p-3">
                                                    <button
                                                        onClick={() => handleDeleteJob(job.id)}
                                                        className="px-2 py-1 bg-red-500 text-white rounded-lg hover:bg-red-600 transition duration-200 flex items-center gap-2"
                                                    >
                                                        <Icon icon="mdi:trash-can" width={16} height={16} />
                                                        Delete
                                                    </button>
                                                </td>
                                            </tr>
                                        ))}
                                        </tbody>
                                    </table>
                                </div>
                            )}
                        </div>
                    </div>
                </div>
            </div>
            <footer
                className={`p-4 text-center text-sm shadow-inner ${
                    mode === "dark" ? "bg-gray-900 text-gray-400" : "bg-white text-gray-500"
                }`}
            >
                © {new Date().getFullYear()} Growthpad Consulting Group. All rights reserved.
            </footer>
        </div>
    );
}